import React, { useRef, useState } from 'react';
import SectionTitle from './section-titles';
import CountUp from 'react-countup';
import VisibilitySensor from 'react-visibility-sensor';

const VideoSection = () => {
  const startRef = useRef(null);
  const [clients, setClients] = useState(0);
  const [coverage, setCoverage] = useState(0);
  const [speed, setSpeed] = useState(0);
  const [homes, setHomes] = useState(0);
  const duration = 5;

  return (
    <>
      <section id='video' className='center'>
        <div className='my-5'>
          <SectionTitle
            undertext={'  ,      ,            20      .'}
          >
            <h2 className='mb-5'>   <span className='text-purple'>   </span></h2>
            <hr className='center' />
          </SectionTitle>

          <VisibilitySensor
            onChange={(isVisible) => {
              if (isVisible) {
                setClients(45);
                setCoverage(78);
                setSpeed(100);
                setHomes(30)
              }
            }}
            delayedCall
          >
            <div className='container'>
              <hr className='normal' />
              <div className='row d-flex justify-content-between text-center'>
                <div className='col-6 col-md-3'>
                  {/* <h2 class='statistics'>45</h2> */}
                  <h2 class='statistics'><CountUp start={0} end={clients} duration={duration} ref={startRef} /></h2>
                  <h3> ()</h3>
                </div>
                <div className='col-6 col-md-3'>
                  <h2 class='statistics'><CountUp start={0} end={coverage} duration={duration} />%</h2>
                  <h3></h3>
                </div>
                <div className='col-6 col-md-3'>
                  <h2 class='statistics'><CountUp start={0} end={speed} duration={duration} /></h2>
                  <h3>/</h3>
                </div>
                <div className='col-6 col-md-3'>
                  <h2 class='statistics'><CountUp start={0} end={homes} duration={duration} /></h2>
                  <h3></h3>
                </div>
              </div>
              <hr className='normal' />
            </div>
          </VisibilitySensor>
        </div>
      </section>
    </>
  )
}

export default VideoSection;