import React, { useRef, useState } from 'react';
import Slider from 'react-slick';
import SectionTitle from './section-titles';
import styled from 'styled-components';

import testimonialBg from '../../images/index/testimonial-bg.svg';
import arrowRight from '../../images/index/right-arrow.svg'
import arrowLeft from '../../images/index/left-arrow.svg'
import constants from '../../constants/index';

import nonActiveItem from '../../images/icons/non-active-list-item.svg';
import activeItem from '../../images/icons/active-list-item.svg';

const TestimonialWrapper = styled.div`
  background-image: url(${(props) => props.testimonialBg});
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  min-height: 540px;
  width: 540px;
  text-align: center;
`;

const TestimonialTextBox = styled.div`
  width: 540px;
`
const TestimonialSlider = () => {
  let sliderRef = useRef(null);
  let itemRef = useRef(null);

  useState(() => {
    console.log(itemRef);
    return () => { };
  }, []);

  const settings = {
    arrows: true,
    speed: 1000,
    autoplaySpeed: 3000,
    infinite: false,
    centerMode: true,
    autoplay: false,
    slidesToScroll: 1,
    focusOnSelect: true,
    pauseOnFocus: true,
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
    responsive: [
      {
        breakpoint: 1200,
        settings: {
          arrows: false,
          slidesToShow: 1,
        }
      },
      {
        breakpoint: 700,
        settings: {
          arrows: false,
          dots: true,
          slidesToShow: 1,
        }
      },
    ]
  }


  return (
    <>
      <section id='testimonials'>
        <div className='row d-flex justify-content-center'>
          <div className='col-12'>
            <SectionTitle>
              <h2> <br /><span className='text-purple'>  </span></h2>
              <hr className='center' />
            </SectionTitle>
          </div>

          <div className='col-12 px-md-4 testimonial'>
            <Slider {...settings} ref={sliderRef}>
              {constants.clientOpinions.map(({ name, opinion, date }, index) => (
                <TestimonialWrapper key={index} testimonialBg={testimonialBg} ref={itemRef} className='d-flex justify-content-center align-items-center'>
                  <TestimonialTextBox className='px-4 pt-5'>
                    <h3>{name}</h3>
                    <p className='text-testimonial'>{opinion}</p>
                    <h5 className='d-none d-sm-inline-block w-100'>{date}</h5>
                    <BulletPoints
                      count={constants.clientOpinions.length}
                      active={index}
                      sliderRef={sliderRef}
                    />
                  </TestimonialTextBox>
                </TestimonialWrapper>
              ))}
            </Slider>
          </div>
        </div>
      </section>
    </>
  )
}

const BulletPoints = ({ count, active, sliderRef }) => {
  let res = [];
  for (var i = 0; i < count; i++) {
    if (i === active) {
      res.push(<li className='d-inline-block mx-1'><img src={activeItem} alt=' ' /></li>)
    } else {
      res.push(<li className='d-inline-block mx-1'><img src={nonActiveItem} alt=' ' /></li>)
    }
  }

  return (
    <div className='d-none d-md-inline-block w-100 pr-5 mt-2'>
      <ul className='inline-list'>
        {res.map((element, index) => (
          <div className='d-inline' key={index} onClick={(e) => { console.log(`goTo: ${index}`); sliderRef.current.slickGoTo(index) }}>
            {element}
          </div>
        ))}
      </ul>
    </div>
  )
}

const NextArrow = ({ className, style, onClick }) => {
  return (
    <div
      // {...props}
      className={className + ' next-arrow'}
      style={{ ...style, display: 'block' }}
      onClick={onClick}
    >
      <img src={arrowRight} className='img-fluid' alt="Arrow Right" />
    </div>
  );
}
const PrevArrow = ({ className, style, onClick }) => {
  return (
    <div
      className={className + ' prev-arrow'}
      style={{ ...style, display: 'block' }}
      onClick={onClick}
    >
      <img src={arrowLeft} className='img-fluid' alt="Arrow Left" />
    </div>
  );
}

export default TestimonialSlider;