import React, { useEffect, useState, useRef } from 'react';
import SectionTitle from './section-titles';
import Slider from 'react-slick';
import styled from 'styled-components';
import VisibilitySensor from 'react-visibility-sensor';


import cardBg from '../../images/index/horizontal-step-bg.svg'
import stepsImage from '../../images/index/steps-1.svg'
import stepsImageSm from '../../images/index/steps-1-sm.svg'
import constants from '../../constants/index';

const HorizontalSteps = () => {
  var sliderRef = useRef(null);
  const [windowInnerWidth, setWindowInnerWidth] = useState(0);
  const [currentSlide, setCurrentSlide] = useState(0);

  useEffect(() => {
    windowResizeListener();
    window.addEventListener('resize', windowResizeListener);

    console.log(sliderRef);
    return () => {
      window.removeEventListener('wheel', trackScroll);
      window.removeEventListener('resize', windowResizeListener);
    };
  }, []);

  useEffect(() => {
    return () => { };
  }, [currentSlide])

  const windowResizeListener = () => {
    setWindowInnerWidth(window.innerWidth);
  }

  const trackScroll = (isVisible) => {
    window.addEventListener('wheel', e => {
      if (isVisible) {
        console.log(currentSlide);
        // if (currentSlide !== (constants.horizontalCards - 1)) {
          // e.preventDefault();
          // e.deltaY > 0 ? sliderRef.current.slickNext() : sliderRef.current.slickPrev();
        // }

      } else {

      }
    })
  }

  const beforeChangeSlider = async (current, next) => {
    console.log(`{current: ${current}, next: ${next}}`)
    setCurrentSlide(next);
  }

  const settings = {
    arrows: false,
    speed: 1000,
    infinite: false,
    centerMode: false,
    autoplay: false,
    autoplaySpeed: 5000,
    slidesToShow: 1,
    slidesToScroll: 1,
    focusOnSelect: true,
    pauseOnFocus: true,
    beforeChange: beforeChangeSlider,
    responsive: [
      {
        breakpoint: 700,
        settings: {
          arrows: false,
          dots: true,
        }
      },
    ]
  }


  return (
    <>
      <section id='horizontal-steps'>
        <div className='row steps-section'>
          <SectionTitle className='text-center pt-5 mt-5'>
            <h2 className='text-white'>     <br /><span className='text-purple'>  </span></h2>
            <hr className='center' />
          </SectionTitle>
          <div className='row w-100 mt-0'>
            <div className='container text-center'>
              <div className='d-none d-xl-block'>
                <img src={stepsImage} className='img-fluid img-negative-top order-0 order-md-1' alt='      Netsurf' />
              </div>
              <div className='row my-xl-5 order-1 order-md-0'>
                <VisibilitySensor onChange={trackScroll}>
                  <div className='col-12'>
                    <Slider {...settings} id='horizontal-slides' ref={sliderRef}>
                      {constants.horizontalCards.map(({ title, icon, text, image }, i) => (
                        <div key={i} className='d-flex justify-content-center'>
                          <Card
                            title={title}
                            icon={icon}
                            text={text}
                            image={image}
                            windowWidth={windowInnerWidth}
                            cardIndex={i}
                          />
                        </div>
                      ))}
                    </Slider>
                    <h6 className='d-none d-xl-block text-white'>   </h6>
                  </div>
                </VisibilitySensor>
                <div className='d-block d-xl-none d-flex justify-content-center w-100 mt-3 mb-5'>
                  <img src={stepsImageSm} className='img-fluid' alt='      Netsurf' />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  )
}

const Card = ({ windowWidth, text, image, title, icon, cardIndex }) => {
  if (windowWidth < 1200) {
    return (
      <>
        <div className='col-12 col-md-7 my-5'>
          <CardWrapperSm>
            <div className='pt-3 text-center'>
              <div className='col-12 d-flex justify-content-center align-items-center'>
                <img src={icon} className='img-fluid' alt='   ' />
              </div>
              <div className='col-12 d-flex justify-content-center align-items-center'>
                {title}
              </div>
              <div className='col-12 mt-1 d-flex justify-content-center align-items-center'>
                <p>{text}</p>
              </div>
            </div>
          </CardWrapperSm>
        </div>
      </>
    )
  } else {
    return (
      <>
        <div className='row d-flex justify-content-end'>
          <div className='col-6'>
            <CardWrapper className='horizontal-steps-card'>
              <CardContainer className='text-left'>
                <div className='row'>
                  <div className='col-4'>
                    <img src={icon} className='img-fluid' alt='   ' />
                  </div>
                  <div className='col-8 d-flex justify-content-start align-items-center'>
                    {title}
                  </div>
                </div>
                <div className='col-12 mt-2 px-3'>
                  <p>{text}</p>
                </div>
              </CardContainer>
            </CardWrapper>
          </div>
          <div className='col-xl-3 d-none d-xl-block'>
            <img src={image} className={'img-fluids card-image-' + cardIndex} alt='   ' />
          </div>
        </div>
      </>
    )
  }
}

const CardWrapperSm = styled.div`
  background: #FFFFFF;
  box-shadow: 0px 0px 102px rgba(17, 41, 70, 0.25);
  border-radius: 16px;
`;

const CardWrapper = styled.div`
  background-image: url(${cardBg});
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
  height: 538px;
  width: auto;
`;

const CardContainer = styled.div`
  padding-top: 110px;
`;

export default HorizontalSteps;